export function createCalendar(elem, year, month) {
    if (elem) {

        let mon = month - 1; // месяцы в JS идут от 0 до 11, а не от 1 до 12
        let d = new Date(year, mon);

        // let table = '<table><tr><th>пн</th><th>вт</th><th>ср</th><th>чт</th><th>пт</th><th>сб</th><th>вс</th></tr><tr>';
        let table = '<div class=\'new-task-setting__calendar-content\'><p class=\'new-task-setting__calendar-block day\'>M</p><p class=\'new-task-setting__calendar-block day\'>T</p><p class=\'new-task-setting__calendar-block day\'>W</p><p class=\'new-task-setting__calendar-block day\'>T</p><p class=\'new-task-setting__calendar-block day\'>F</p><p class=\'new-task-setting__calendar-block day\'>S</p><p class=\'new-task-setting__calendar-block day\'>S</p>'

        // пробелы для первого ряда
        // с понедельника до первого дня месяца
        // * * * 1  2  3  4
        for (let i = 0; i < getDay(d); i++) {
            table += '<p class=\'new-task-setting__calendar-block\'></p>';
        }

        // <td> ячейки календаря с датами
        while (d.getMonth() == mon) {
            table += '<button id="button" class=\'new-task-setting__calendar-block button\'>' + d.getDate() + '</button>';
            // if (getDay(d) % 7 == 6) { // вс, последний день - перевод строки
            // table += '</tr><tr>';
            // }

            d.setDate(d.getDate() + 1);
        }

        // добить таблицу пустыми ячейками, если нужно
        // 29 30 31 * * * *
        if (getDay(d) != 0) {
            for (let i = getDay(d); i < 7; i++) {
                table += '<p class=\'new-task-setting__calendar-block\'></p>';
            }
        }

        // закрыть таблицу
        table += '</div>';

        elem.innerHTML = table;
    }
}

export function getDay(date) { // получить номер дня недели, от 0 (пн) до 6 (вс)
    let day = date.getDay();
    if (day == 0) day = 7; // сделать воскресенье (0) последним днем
    return day - 1;
}

